<template>
	<div class="moudle">
		<h2><span class="title">港股通涨幅排行榜</span></h2>
		<div class="table">
			<table border="0" cellspacing="0" cellpadding="0">
				<tr>
					<th width="10%">排名</th>
					<th>代码</th>
					<th>股票名称</th>
					<th >涨幅<span>%</span></th>
					<th>现价</th>
					<th width="25%">成交金额<span>(亿)</span></th>
				</tr>
				<tr v-for="(item,index) in table" :key="index">
					<td>{{index+1}}</td>
					<td>{{item.SecuCode}}</td>
					<td>{{item.SecuAbbr}}</td>
					<td>{{item.ChangePCT}}%</td>
					<td>{{item.ClosePrice}}</td>
					<td>{{item.TurnValue}}</td>
				</tr>
			</table>
		</div>
	</div>
</template>

<script>
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	var storage=window.localStorage;
	storage.id=undefined;
	export default {
		data(){
			return{
				table:[],
			}
		},
		created() {
			this.changeData();
		},
		methods:{
			changeData(){
				fetchGet(URL.increase.url,URL.increase.params).then(res=>{
					var res = res.data;
					if(res.code == "000000"){
						//this.table = res.data;
						for(var i=0;i<res.data.length;i++){
							if(i<10){
								this.table.push(res.data[i])
							}
						}
					}
				})
			}
		}
	}
</script>
