<template>
	<div class="moudle">
		<div class="widthCenter">
			<h2><span class="title">当日资讯</span></h2>
			<ul class="news">
				<li v-for="item in content"><i></i><span>{{item}}</span></li>
			</ul>
		</div>
	</div>
</template>

<script>
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	
	export default {
		data(){
			return {
				content:[]
			}
		},
		created(){
			fetchGet(URL.getNewsByReportDate.url,URL.getNewsByReportDate.params).then(res=>{
				var res = res.data;
				if(res.code == "000000"){
					this.content = res.responseEntity[0].content.split('\n');
				}
			})
		}
	}
</script>
