<template>
	<div class="table">
		<table border="0" cellspacing="0" cellpadding="0">
			<tr>
				<th>方向</th>
				<th>类型</th>
				<th>总成交</th>
				<th>买入成交</th>
				<th>卖出成交</th>
				<th>净流入</th>
				<th>日余额</th>
			</tr>
			<tr v-for="(item,index) in table" :key="index">
				<td>{{item.Flow_Dire}}</td>
				<td>{{item.Plate_Type}}</td>
				<td>{{item.Total_Amount}}</td>
				<td>{{item.Buy_Amount}}</td>
				<td>{{item.Sell_Amount}}</td>
				<td>{{item.Net_Amount}}</td>
				<td>{{item.Remain_Amount}}</td>
			</tr>
		</table>
	</div>
</template>

<script>
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	var storage=window.localStorage;
	storage.id=undefined;
	export default {
		data(){
			return{
				table:[],
			}
		},
		created() {
			this.changeData();
		},
		methods:{
			changeData(){
				fetchGet(URL.capital.url,URL.capital.params).then(res=>{
					var res = res.data;
					if(res.code == "000000"){
						//this.table = res.data;
						for(var i=0;i<res.data.length;i++){
							if(i<10){
								this.table.push(res.data[i])
							}
						}
					}
				})
			}
		}
	}
</script>