<template>
	<div>
		<div class="Tenth_top widthCenter">
			<span class="text">港股通十大成交股</span>
			<ul>
				<li :class="{active:isActive == 0}" @click="changeData(isActive = 0)">沪市</li>
				<li :class="{active:isActive == 1}" @click="changeData(isActive = 1)">深市</li>
			</ul>
		</div>
		<div class="table table2">
			<table border="0" cellspacing="0" cellpadding="0">
				<tr>
					<th>排名</th>
					<th>代码</th>
					<th>股票名称</th>
					<th>
						净买入额
						<span>(亿)</span>
					</th>
					<th>
						买入金额
						<span>(亿)</span>
					</th>
					<th>
						卖出金额
						<span>(亿)</span>
					</th>
				</tr>
				<tr v-for="(item,index) in table" :key="index">
					<td>{{index+1}}</td>
					<td>{{item.SecuCode}}</td>
					<td>{{item.SecuAbbr}}</td>
					<td>{{item.NetAmount}}</td>
					<td>{{item.BuyAmount}}</td>
					<td>{{item.SellAmount}}</td>
				</tr>
			</table>
		</div>
	</div>
</template>

<script>
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	var storage=window.localStorage;
	storage.id=undefined;
	export default {
		data(){
			return{
				table:[],
				isActive:0
			}
		},
		created() {
			this.changeData(0);
		},
		methods:{
			changeData(index){
				if(index == storage.id){
					return false;
				}else{
					storage.id=index;
				}
				
				fetchGet(URL.position.url,URL.position.params.filter[index]).then(res=>{
					var res = res.data;
					if(res.code == "000000"){
						this.table = res.data;
					}
				})
			}
		}
	}
	
</script>
