<template>
	<div class="charts_box" id="myChart1">
		
	</div>
</template>

<script>
	import echarts from 'echarts'
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	export default {
		data(){
			return{
				data:[],
				date:[],
				Min:'',
				Max:''
			}
		},
		created() {
			fetchGet(URL.exchangeChart.url,URL.exchangeChart.params).then(res=>{
				var res = res.data;
				if(res.code == "000000"){
					for(let i=0 ;i<res.data.length;i++){
						this.data.push(res.data[i].ExchangeRate);
						this.date.push(res.data[i].TradingDay);
						this.Min = (Math.min(this.data)-10);
						this.Max = (Math.max(this.data)+10);
					}
					this.getCharts();
				}
			})
		},
		
		methods:{
			getCharts(){
				let myChart1 = this.$echarts.init(document.getElementById('myChart1'));
				var option = {
					title: {
						text: '100港元兑人民币汇率走势',
						left:'center'
					},
					tooltip: {
						trigger: 'axis',
						show:false
					},
					legend: {
						data:["100港元兑人民币汇率走势"],
						top:'30px'
					},
					grid: {
						top:"80px",
						left: '3%',
						right: '4%',
						bottom: '3%',
						containLabel: true
					},
					toolbox: {
						feature: {
							saveAsImage: {}
						}
					},
					xAxis: {
						type: 'category',
						boundaryGap: false,
						data: this.date
					},
					yAxis: {
						type: 'value',
						min: 80,
						//max: this.Max
					},
					series: [
						{
							name:'100港元兑人民币汇率走势',
							type:'line',
							showSymbol: false,
							stack: '总量',
							data:this.data
						},
						
					]
				};
				myChart1.setOption(option);
			}
		}
	}
</script>

<style scoped>
	.charts_box{background: #FFFFFF !important;height: 4.6rem !important;}
	
</style>