<template>
	<div class="moudle">
		<h2><span class="title">AH股折溢价</span></h2>
		<div class="widthCenter">
			<div class="remark">截至上一交易日收盘，AH股折溢价指数为116.78，AH折溢价指数略下降，H 股相对A股仍有一定折价空间。</div>
			<div class="charts_box" id="myChart4"></div>
		</div>
	</div>
</template>

<script>
	import echarts from 'echarts'
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	export default {
		data(){
			return{
				data:[],
				date:[],
			}
		},
		created() {
			fetchGet(URL.AH.url,URL.AH.params).then(res=>{
				var res = res.data;
				if(res.code == "000000"){
					for(let i=0 ;i<res.data.length;i++){
						this.data.push(res.data[i].ClosePrice);
						this.date.push(res.data[i].TradingDay);
					}
					this.getCharts();
				}
			})
		},
		
		methods:{
			getCharts(){
				let myChart4 = this.$echarts.init(document.getElementById('myChart4'));
				var option = {
					title: {
						text: 'AH股溢价指数',
						left:'center'
					},
					tooltip: {
						trigger: 'axis',
						show:false
					},
					color: ["#4d88ec"],
					legend: {
						
						data:["收盘价"],
						top:'30px'
					},
					grid: {
						top:"80px",
						left: '3%',
						right: '4%',
						bottom: '3%',
						containLabel: true
					},
					toolbox: {
						feature: {
							saveAsImage: {}
						}
					},
					xAxis: {
						type: 'category',
						boundaryGap: false,
						data: this.date
					},
					yAxis: {
						type: 'value',
					},
					series: [
						{
							name:'收盘价',
							type:'line',
							showSymbol: false,
							stack: '总量',
							lineStyle:{
								normal:{
									color: '#4d88ec'
								}
							},
							data:this.data
						},
						
					]
				};
				myChart4.setOption(option);
			}
		}
	}
</script>

