<template>
	<div class="table table3">
		<table border="0" cellspacing="0" cellpadding="0">
			<tr>
				<th>排名</th>
				<th>代码</th>
				<th>股票名称</th>
				<th>持仓比例</th>
			</tr>
			<tr v-for="(item ,index) in table" :key="index">
				<td>{{index+1}}</td>
				<td>{{item.SecuAbbr}}</td>
				<td>{{item.SecuCode}}</td>
				<td>{{item.HoldingPSI}}%</td>
			</tr>
		
		</table>
	</div>
</template>

<script>
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	var storage=window.localStorage;
	storage.id=undefined;
	export default {
		data(){
			return{
				table:[],
			}
		},
		created() {
			this.changeData();
		},
		methods:{
			changeData(){
				
				fetchGet(URL.positionNew.url,URL.positionNew.params).then(res=>{
					var res = res.data;
					if(res.code == "000000"){
						//this.table = res.data;
						for(var i=0;i<res.data.length;i++){
							if(i<10){
								this.table.push(res.data[i])
							}
						}
					}
				})
			}
		}
	}
</script>
