<template>
	<div class="moudle">
		<div class="widthCenter">
			<h2><span class="title">宏观/政策信息 </span></h2>
			<ul class="news ">
				<li v-for="(item,index) in table" :key="index"><i></i><span class="black">{{item.title}}</span>
					<div class="news_info">{{item.content}}<p></p>
					</div>
					<div class="read">
						<font>解读：</font>{{item.answer}}
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	var storage=window.localStorage;
	storage.id=undefined;
	export default {
		data(){
			return{
				table:[],
			}
		},
		created() {
			this.changeData();
			// this.$set(this.table,[])
		},
		methods:{
			changeData(){
				fetchGet(URL.transaction.url,URL.transaction.params.filter[3]).then(res=>{
					var res = res.data;
					console.log(res)
					if(res.code == "000000"){ 
						this.table = [...res.responseEntity];
					}
				})
			}
		}
	}
</script>
