<template>
	<div class="table table1">
		<table border="0" cellspacing="0" cellpadding="0">
			<tr>
				<th width="30%">指数</th>
				<th>收盘价</th>
				<th>涨跌幅</th>
				<th>成交额</th>
			</tr>
			<tr v-for="(item,index) in table" :key="index">
				<td>{{item.IndexName}}</td>
				<td>{{item.ClosePrice}}</td>
				<td>{{item.ChangePCT}}</td>
				<td>{{item.TurnoverValue}}</td>
			</tr>
		</table>
	</div>
</template>

<script>
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	export default {
		data(){
			return{
				table:[]
			}
		},
		created() {
			fetchGet(URL.bazaar.url,URL.bazaar.params).then(res=>{
				var res = res.data;
				if(res.code == "000000"){
					this.table = res.data;
				}
			})
		}
	}
</script>

