<template>
	<div class="moudle">
		<div class="widthCenter">
			<h2><span class="title">研报观点 </span></h2>
			<ul class="news ">
				<li v-for="(item, index) in table" :key="index"><i></i><span>{{item}}</span></li>
			</ul>
		</div>
	</div>
</template>

<script>
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	var storage=window.localStorage;
	storage.id=undefined;
	export default {
		data(){
			return{
				table:[],
			}
		},
		created() {
			this.changeData();
		},
		methods:{
			changeData(){
				fetchGet(URL.transaction.url,URL.transaction.params.filter[2]).then(res=>{
					var res = res.data;
					if(res.code == "000000"){
						this.table = res.responseEntity[0].content.split('\n');
					}
				})
			}
		}
	}
</script>
