<template>
	<div class="moudle">
		<div class="widthCenter">
			<ul  class="m_tabs">
				<li :class="{active:isActive == 0}" @click="changeData(isActive =0)">快速游览</li>
				<li :class="{active:isActive == 1}"  @click="changeData(isActive =1)">股价异动</li>
				<li :class="{active:isActive == 2}"  @click="changeData(isActive =2)">回购速递</li>
			</ul>
			<ul class="news">
				<li  v-for="(item,index) in table" :key="index"><i></i><span>{{item}}</span></li> 
			</ul>
		</div>
	</div>
</template>

<script>
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	var storage=window.localStorage;
	storage.id=undefined;
	export default {
		data(){
			return{
				table:[],
				isActive:0
			}
		},
		created() {
			this.changeData(0);
		},
		methods:{
			changeData(index){
				if(index == storage.id){
					return false;
				}else{
					storage.id=index;
					if(index!=2){
						fetchGet(URL.transaction.url,URL.transaction.params.filter[index]).then(res=>{
							var res = res.data;
							if(res.code == "000000"){
								this.table = res.responseEntity[0].content.split('\n');
							}
						})
					}else{
						fetchGet(URL.buyBack.url,URL.buyBack.params).then(res=>{
							var res = res.data;
							if(res.code == "000000"){
								var data =[]
								
								for(var i=0;i<res.data.length;i++){
									data.push(res.data[i].RepNote);
								}
								this.table = data;
							}
						})
					}
				}
				
				
			}
		}
	}
	
</script>

