<template>
	<div class="table">
		<table border="0" cellspacing="0" cellpadding="0">
			<tr>
				<th width="30%">美元/人民币-涨跌(BP)</th>
				<th width="30%">港币/人民币-涨跌(BP)</th>
				<th>买入价(HKD)</th>
				<th>卖出价(HKD)</th>
			</tr>
			<tr>
				<td>{{ClosePrice}} <span class="red">{{ChangePCT}}</span></td>
				<td>{{ChangePCT2}} <span class="red">{{ChangePCT2}}</span></td>
				<td>{{ClosePrice3}}</td>
				<td>{{ClosePrice4}}</td>
			</tr>
		</table>
	</div>	
</template>

<script>
	//exchange
	
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	export default {
		data(){
			return{
				ClosePrice:'',
				ClosePrice2:'',
				ChangePCT:'',
				ChangePCT2:'',
				ClosePrice3:'',
				ClosePrice4:''
			}
		},
		created() {
			fetchGet(URL.exchange.url,URL.exchange.params.filter[0]).then(res=>{
				var res = res.data;
				if(res.code == "000000"){
					//this.table = res.data;
					for(let i=0;i<res.data.length;i++){
						if(res.data[i].IndexCode =="USD/CNY"){
							this.ClosePrice = res.data[i].ClosePrice;
							this.ChangePCT = res.data[i].ChangePCT;
						}
						if(res.data[i].IndexCode =="HKD/CNY"){
							this.ClosePrice2 = res.data[i].ClosePrice;
							this.ChangePCT2 = res.data[i].ChangePCT;
						}
						if(res.data[i].IndexCode =="HKDBUY"){
							this.ClosePrice3 = res.data[i].ClosePrice;
						}
						if(res.data[i].IndexCode =="HKDSELL"){
							this.ClosePrice4 = res.data[i].ClosePrice;
						}
					}
				}
			})
		}
	}
</script>
