<template>
	<div class="charts_box" id="myChart2"></div>
</template>

<script>
	import echarts from 'echarts'
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	export default {
		data(){
			return{
				date:[],
				series:[
					{data:[]},
					{data:[]},
					{data:[]}
				]
			}
		},
		created() {
			fetchGet(URL.capitalCharts.url,URL.capitalCharts.params).then(res=>{
				var res = res.data;
				if(res.code == "000000"){
					for(let i=0 ;i<res.data.length;i++){
						this.date.unshift(res.data[i].TradingDay);
						this.series[0].data.unshift(res.data[i].SHHKSCNet);
						this.series[1].data.unshift(res.data[i].HSICPrice);
						this.series[2].data.unshift(res.data[i].SHHKSZNet);
					}
					this.getCharts();
				}
			})
		},
		
		methods:{
			getCharts(){
				let myChart2 = this.$echarts.init(document.getElementById('myChart2'));
				var option = {
					title: {
						text: '港股通资金流入及恒生指数走势图	',
						left:'center'
					},
					tooltip: {
						trigger: 'axis',
						show:false
					},
					legend: {
						data:["港股通（沪）当日资金净流入（亿）","恒生指数","港股通（深）当日资金净流入（亿）"],
						top:'30px'
					},
					grid: {
						top:"80px",
						left: '3%',
						right: '3%',
						bottom: '3%',
						containLabel: true
					},
					toolbox: {
						feature: {
							saveAsImage: {}
						}
					},
					xAxis: {
						type: 'category',
						boundaryGap: false,
						// data: this.date,
						//  axisLabel : {//坐标轴刻度标签的相关设置。
						// 	interval:0,
						// 	rotate:"45"
						// }
					},
					yAxis: [
						{
						name:'',
						type: 'value',
						axisLine:{
			                show:false
			            },
			            axisTick:{
			                show:false
			            },
			            splitLine:{
			                show:false
			            }
					  },
					  {
						name:'',
						type:'value',
						axisLine:{
			                show:false
			            },
			            axisTick:{
			                show:false
			            },
			            splitLine:{
			                show:false
			            }
					  }],
					series: [
						{
							name:'港股通（沪）当日资金净流入（亿）',
							type:'bar',
							barGap:"100%",
							barWidth:'2px',
							showSymbol: false,
							stack: '总量',
							yAxisIndex:0,
							data:this.series[0].data
						},
						{
							name:'恒生指数',
							type:'line',
							yAxisIndex:1,
							showSymbol: false,
							stack: '总量',
							data:this.series[1].data
						},
						{
							name:'港股通（深）当日资金净流入（亿）',
							type:'bar',
							yAxisIndex:0,
							barWidth:'2px',
							showSymbol: false,
							stack: '总量',
							data:this.series[2].data
						},
						
					]
				};
				myChart2.setOption(option);
			}
		}
	}
</script>

