<template>
	<div class="charts_box" id="myChart">
		
	</div>
</template>

<script>
	import echarts from 'echarts'
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	export default {
		data(){
			return{
				charts:[],
				date:[],
				title:[],
				series:[
					{data:[]},
					{data:[]},
					{data:[]}
				],
				
			}
		},
		created() {
			fetchGet(URL.bazaarChart.url,URL.bazaarChart.params).then(res=>{
				var res = res.data;
				if(res.code == "000000"){
					for(let i=0 ;i<res.data.length;i++){
						this.date.push(res.data[i].TradingDay);
						if(i==0){
							this.title.push(res.data[i].IndexNameHS,res.data[i].IndexNameSZZ,res.data[i].IndexNameHSZS)
						}
						this.series[0].data.push(parseFloat(res.data[i].ChangePCTRBHS));
						this.series[1].data.push(parseFloat(res.data[i].ChangePCTRBSZZ));
						this.series[2].data.push(parseFloat(res.data[i].ChangePCTRBHSZS));
						this.charts.push(res.data[i])
					}
					this.getCharts();
				}
			})
		},
		
		methods:{
			getCharts(){
				let myChart = this.$echarts.init(document.getElementById('myChart'));
				var option = {
					title: {
						text: '主要指数走势对比图',
						left:'center'
					},
					tooltip: {
						trigger: 'axis',
						show:false
					},
					legend: {
						data:this.title,
						top:'30px'
					},
					grid: {
						top:"80px",
						left: '3%',
						right: '4%',
						bottom: '3%',
						containLabel: true
					},
					toolbox: {
						feature: {
							saveAsImage: {}
						}
					},
					xAxis: {
						type: 'category',
						boundaryGap: false,
						data: this.date
					},
					yAxis: {
						type: 'value',
						 axisLabel:{
						    formatter:'{value} %'
					    }
					},
					series: [
						{
							name:'恒生综合指数',
							type:'line',
							stack: '总量',
							data:this.series[0].data
						},
						{
							name:'上证指数',
							type:'line',
							stack: '总量',
							data:this.series[1].data
						},
						{
							name:'恒生指数',
							type:'line',
							stack: '总量',
							data:this.series[2].data
						}
					]
				};
				myChart.setOption(option);
			}
		}
	}
</script>

<style scoped>
	.charts_box{background: #FFFFFF !important;height: 4.6rem !important;}
	
</style>