<template>
	<div class="charts_box" id="myChart3"></div>
</template>

<script>
	import echarts from 'echarts'
	import {fetchGet} from '@/api/common.js'
	import URL from '@/api/api.js'
	export default {
		data(){
			return{
				data:[],
				date:[],
				series:[
					{data:[]},
					{data:[]},
					{data:[]}
				]
			}
		},
		created() {
			fetchGet(URL.capitalCharts.url,URL.capitalCharts.params).then(res=>{
				var res = res.data;
				if(res.code == "000000"){
					for(let i=0 ;i<res.data.length;i++){
						this.date.unshift(res.data[i].TradingDay);
						this.series[0].data.unshift(res.data[i].HSSENet);
						this.series[1].data.unshift(res.data[i].SHHKNet);
					}
					this.getCharts();
				}
			})
		},
		
		methods:{
			getCharts(){
				let myChart3 = this.$echarts.init(document.getElementById('myChart3'));
				var option = {
					title: {
						text: '近三月南北向资金趋势图',
						left:'center'
					},
					tooltip: {
						trigger: 'axis',
						show:false
					},
					legend: {
						data:["北向资金趋势（亿元）","南向资金趋势（亿港元）"],
						top:'30px'
					},
					grid: {
						top:"80px",
						left: '3%',
						right: '3%',
						bottom: '3%',
						containLabel: true
					},
					toolbox: {
						feature: {
							saveAsImage: {}
						}
					},
					xAxis: {
						type: 'category',
						boundaryGap: false,
						data: this.date
					},
					yAxis:{
						type: 'value',
						axisLine:{
						    show:false
			            },
			            axisTick:{
			                show:false
			            },
			            splitLine:{
			                show:false
			            }
					  },
					series: [
						{
							name:'北向资金趋势（亿元）',
							type:'line',
							barGap:"100%",
							barWidth:'2px',
							showSymbol: false,
							stack: '总量',
							data:this.series[0].data
						},
						{
							name:'南向资金趋势（亿港元）',
							type:'line',
							showSymbol: false,
							stack: '总量',
							data:this.series[1].data
						},
					]
				};
				myChart3.setOption(option);
			}
		}
	}
</script>

